@import url(https://fonts.googleapis.com/css2?family=Poppins&display=swap);
.searchBar {
    background-color: #fff;
    border-radius: 50px;
    border: 1px solid #f0f0f0;
    margin: 0 auto 100px auto;
    padding: 2px 10px;
    width: 80%;
    height: 50px;

    display: flex;
    flex-flow: row nowrap;
    align-items: center;
}

.searchBar input {
    height: 25px;
    padding: 0 10px;
    width: 100%;

    border: none;
}

.searchBar input:focus {
    outline: none;
}

.searchBar button {
    border: none;
    background: transparent;
    cursor: pointer;
}

.searchBar button svg {
    transition: 0.2s opacity;
}

.searchBar button svg:hover {
    opacity: 50%;
}

@media only screen and (max-width: 769px) {
    .searchBar {
        width: 90%;
        margin-bottom: 50px;
    }
}

.itemContainer {
    background: rgba(51,51,51,0.9);
    border-radius: 8px;
    padding: 10px;
    display: flex;
    justify-self: center;
    flex-flow: column nowrap;
    align-items: center;
    justify-content: space-between;
}

.itemContainer img {
    width: 300px;
    height: 444px;
}

.itemContainer h1 {
    width: 100%;
    max-width: 300px;
    margin-top: 5px;
    font-size: 1.6rem;
    text-align: center;
}

.itemContainer a {
    text-decoration: none;
    justify-self: flex-start;
}

.itemContainer a button {
    display: flex;
    flex-flow: row nowrap;
    align-items: center;
}

.itemContainer a button svg {
    margin-left: 5px;
}

.loader {
    display: block;
    margin: 0 auto;
    -webkit-animation: 2s spinning infinite linear;
            animation: 2s spinning infinite linear;
}

@-webkit-keyframes spinning {
    to {
        transform: rotate(360deg);
    }
}

@keyframes spinning {
    to {
        transform: rotate(360deg);
    }
}

.moviesListContainer {
    width: 100%;

    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    grid-gap: 50px;
}

@media only screen and (max-width: 1200px) {
    .moviesListContainer {
        grid-template-columns: 1fr 1fr;
    }
}

@media only screen and (max-width: 769px) {
    .moviesListContainer {
        grid-template-columns: 1fr;
    }
}

.searchContainer {
    width: 100%;
    max-width: 1200px;
    margin: 20vh auto 0 auto;
}

@media only screen and (max-width: 769px) {
    .searchContainer {
        margin-top: 5vh;
    }
}

.movieInfoContainer {
    height: 100vh;
    /* width: 100vw; */

    display: flex;
    align-items: center;
    justify-content: center;
}

.movieInfoContainer .movieInfo {
    width: 75%;
    background: rgba(51,51,51,0.9);
    border-radius: 8px;
    padding: 10px;

    display: grid;
    grid-template-columns: 1fr 2fr;
    grid-template-areas: 
        "title title"
        "poster info"
        "about about"
        "link link";
    grid-gap: 5px;
}


.movieInfoContainer .movieInfo h1 {
    grid-area: title;

    font-size: 2rem;
    text-align: center;
}

.movieInfoContainer .movieInfo img {
    grid-area: poster;
    border: 1px solid #777;
    border-radius: 2px;
    padding: 15px;

    width: 300px;
    height: 444px;
}

.movieInfoContainer .movieInfo .info {
    grid-area: info;
    border: 1px solid #777;
    border-radius: 2px;

    display: flex;
    flex-flow: column nowrap;
    align-items: stretch;
}

.movieInfoContainer .movieInfo .info div {
    border: 1px solid #777;
    padding: 2px 10px;
    flex: 1 1;

    display: flex;
    flex-flow: row nowrap;
    align-items: stretch;

}

.movieInfoContainer .movieInfo .info div h2 {
    font-size: 0.8rem;
    margin-right: 10px;
}

.movieInfoContainer .movieInfo .info div p {
    font-size: 0.8rem;
}

.movieInfoContainer .movieInfo .about {
    grid-area: about;
    padding: 2px 10px;
}

.movieInfoContainer .movieInfo .about h2 {
    font-size: 1.6rem;
}

.movieInfoContainer .movieInfo a {
    grid-area: link;
    margin: 0 auto;
    text-decoration: none;
}

@media only screen and (max-width: 1200px) {
    .movieInfoContainer {
        height: auto;
    }
}

@media only screen and (max-width: 769px) {
    .movieInfoContainer .movieInfo {
        width: 100%;
        grid-template-columns: 1fr;
        grid-template-areas: 
        "title"
        "poster"
        "info"
        "about"
        "link";
    }

    .movieInfoContainer .movieInfo img {
        justify-self: center;
    }
}

:root {
    color: #f0f0f0;
    font-size: 16px;
    font-family: 'Poppins', sans-serif;
}

* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

body {
    background: url(/movie-finder/static/media/background.70435b9e.png);
    background-repeat: no-repeat;
    background-attachment: fixed;
    background-size: cover;
}

.standartButton {
    border-radius: 8px;
    border: none;
    background-color: #1089ff;
    padding: 10px;
    margin-top: 10px;
    font-size: 1.5rem;
    color: #f0f0f0;
    cursor: pointer;
    transition: 0.2s -webkit-filter;
    transition: 0.2s filter;
    transition: 0.2s filter, 0.2s -webkit-filter;
    -webkit-filter: brightness(90%);
            filter: brightness(90%);
}

.standartButton:hover {
    -webkit-filter: brightness(120%);
            filter: brightness(120%);
}

@media only screen and (max-width: 769px) {
    body {
        /* background: black; */
    }
}

